import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { HfButtonComponent, HfCardComponent } from '@monorepo/ui';
import { Subscription } from 'rxjs';
import { environment } from '../../../environments/environment';
import { ActivatedRoute, RouterLink } from '@angular/router';
import { OrderStatus } from '../../shared/models/enums';
import { CommonModule } from '@angular/common';
import { URLS } from '../../shared/utils/urls';
import { dayIdFormatted } from "../../shared/utils/day-id-pipe";
import { OrderInvoiceComponent } from "../../shared/components/order-invoice/order-invoice.component";

@Component({
  selector: 'app-order-details',
  standalone: true,
  templateUrl: './order-details.component.html',
  styleUrl: './order-details.component.scss',
  imports: [CommonModule, HfCardComponent, HfButtonComponent, RouterLink, dayIdFormatted, OrderInvoiceComponent]
})

export class OrderDetailsComponent implements OnInit {

  orderbyIdApiSubscription: Subscription;
  orderbyIdApiData: object;
  invoiceOilOrder: object;
  orderId: string;
  orderStatus = OrderStatus;
  urls = URLS;

  constructor(
    private http: HttpClient,
    private route: ActivatedRoute,
  ) { }

  ngOnInit(): void {
    this.orderId = this.route.snapshot.params['orderId'];
    this.callOrderbyIdApi();
  }

  callOrderbyIdApi() {
    // https://dev-api.heatfleet.com/api/customer/order/get?orderId=54556;
    const orderbyIdApiUrl = `${environment.api_url}customer/order/get?orderId=${this.orderId}`;
    this.orderbyIdApiSubscription = this.http.get(orderbyIdApiUrl).subscribe({
      next: (res) => {
        this.orderbyIdApiData = res
        if (this.orderbyIdApiData['oilOrderParameters']['deliveredOrder']) {
          this.invoiceOilOrder = this.orderbyIdApiData['oilOrderParameters']['deliveredOrder']
        } else {
          this.invoiceOilOrder = this.orderbyIdApiData['oilOrderParameters']['placedOrder'];
        }
      },
      error: (err) => console.log(err),
    })
  }
}
